import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers } from '@angular/http';

import { Translation, Tag, Feature, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';


@Component({
  selector: 'my-feature',
  template: require('./feature.component.html'),
  styles: [require('./feature.component.scss')]
})
export class FeatureComponent implements OnInit {

  /**
   * router params
   * id - model id
   * action - "create" or "update"
   */
  id: number;
  action: string;

  feature: Feature = new Feature(0, 0, [], 'Choose one...');

  tags: any[] = [];

  tag: any = {
    id: 0,
    name: "Choose one...",
  };

  // constructor
  constructor(private route: ActivatedRoute,
    private http: Http,
    private api: ApiService,
    private shared: SharedService,
    private data: DataService,
    private config: ConfigService) {
  }

  ngOnInit(): void {

    console.log("Initiated feature component");

    this.route.params.forEach((params: Params) => {
      this.action = params['action'];
      this.id = params['id'];
    });

    this.tags = this.data.tags.map(t => {
      return {
        id: t.id,
        name: t.names.filter(tr => tr.language.id == this.config.language.id)[0].value
      }
    });

    if (this.action == 'edit') {
      this.api.getFeature(this.id).then(feature => {
        this.feature = new Feature(feature.id, feature.tag_id, [], feature.rule);
        for (let i = 0; i < feature.info.length; i++) {
          let language = this.data.languages.filter(lang => lang.active && lang.id == feature.info[i].pivot.language_id)[0];
          if (language != undefined) {
            let translation = new Translation(language, feature.info[i].pivot.name);
            this.feature.names.push(translation);
          }
        }

        if (feature.tag_id !== null && feature.tag_id !== undefined) {
          let tag = this.data.tags.filter(t => t.id == feature.tag_id)[0];
          this.tag.id = tag.id;
          this.tag.name = tag.names.filter(tr => tr.language.id == this.config.language.id)[0].value;
        }
      });
    } else {
      for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
        this.feature.names.push(new Translation(this.data.languages[i], ''));
      }
    }
  }

  save() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (this.action == 'edit')
      this.http.put('feature/' + this.feature.id, {
        data: this.feature
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, "success", "Feature saved successfully!"));
        this.data.updateFeatures();
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Feature could not be saved. ERROR -> " + error._body));
      });
    else
      this.http.post('feature', {
        data: this.feature
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, "success", "Feature saved successfully!"));
        this.data.updateFeatures();
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Feature could not be saved. ERROR -> " + error._body));
      });
  }

  delete() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.delete('feature/' + this.feature.id, { headers })
      .subscribe(() => { }, () => {
        this.data.updateFeatures();
        this.shared.addAlert(new Alert(0, "danger", "Feature could not be deleted due to foreign key constraints"));
      });
  }

  changeTag(id: number) {
    let tag = this.data.tags.filter(t => t.id == id)[0];
    this.tag.id = tag.id;
    this.feature.tag_id = this.tag.id;
    this.tag.name = tag.names.filter(tr => tr.language.id == this.config.language.id)[0].value;
  }

  changeRule(rule: string) {
    this.feature.rule = rule;
  }



}



