import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers } from '@angular/http';

import { Translation, Nutrient, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';




@Component({
  selector: 'my-nutrient',
  template: require('./nutrient.component.html'),
  styles: [require('./nutrient.component.scss')]
})
export class NutrientComponent implements OnInit {

  /**
   * router params
   * id - model id
   * action - "create" or "update"
   */
  id: number;
  action: string;

  nutrient: Nutrient = new Nutrient(0, [], 0, 0, 0);

  measures: any[] = [];

  selectedMeasure = {
    id: 0,
    short: "Choose one..."
  }

  constructor(private route: ActivatedRoute,
    private api: ApiService,
    private config: ConfigService,
    private data: DataService,
    private shared: SharedService,
    private http: Http) {
  }

  ngOnInit(): void {
    console.log("Nutrient component initialized!");

    this.route.params.forEach((params: Params) => {
      this.action = params['action'];
      this.id = params['id'];
    });

    this.api.getMeasures().then(measures => {
      for (let i = 0; i < measures.length; i++) {
        this.measures.push({
          id: measures[i].id,
          short: measures[i].short,
        });
        this.selectedMeasure = this.measures[0];
      }
    })

    if (this.action == 'edit') {
      this.api.getNutrient(this.id).then(nutrient => {
        this.nutrient = new Nutrient(nutrient.id, [], nutrient.dailyrec, nutrient.calories, nutrient.measure_id)
        for (let i = 0; i < nutrient.info.length; i++) {
          let language = this.data.languages.filter(lang => lang.active && lang.id == nutrient.info[i].pivot.language_id)[0];
          if (language !== undefined) {
            let translation = new Translation(language, nutrient.info[i].pivot.name);
            this.nutrient.names.push(translation);
          }
        }

        this.selectedMeasure = this.measures.filter(m => m.id == nutrient.measure_id)[0]
      });
    } else {
      for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
        this.nutrient.names.push(new Translation(this.data.languages[i], ''));
      }
    }
  }

  save() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (this.action == 'edit')
      this.http.put('nutrient/' + this.nutrient.id, {
        data: this.nutrient
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, "success", "Nutrient saved successfully!"));
        this.data.updateNutrients();
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Nutrient could not be saved. ERROR -> " + error._body));
      });
    else
      this.http.post('nutrient', {
        data: this.nutrient
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, "success", "Nutrient saved successfully!"));
        this.data.updateNutrients();
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Nutrient could not be saved. ERROR -> " + error._body));
      });
  }

  delete() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.delete('nutrient/' + this.nutrient.id, { headers })
      .subscribe(() => { }, () => {
        this.data.updateNutrients();
        this.shared.addAlert(new Alert(0, "danger", "Nutrient could not be deleted due to foreign key constraints"));
      });
  }

  changeMeasure(id: Number) {
    this.selectedMeasure = this.measures.filter(m => m.id == id)[0];
    this.nutrient.measure_id = this.selectedMeasure.id;
  };

}



