import { RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { SettingsComponent } from './settings/settings.component';
import { ProductComponent } from './product/product.component';
import { IngredientComponent } from './ingredient/ingredient.component';
import { NutrientComponent } from './nutrient/nutrient.component';
import { TagComponent } from './tag/tag.component';
import { FeatureComponent } from './feature/feature.component';
import { CountryComponent } from './country/country.component';
import { MeasureComponent } from './measure/measure.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'settings', component: SettingsComponent},
  { path: 'product/:action/:id', component: ProductComponent },
  { path: 'nutrient/:action/:id', component: NutrientComponent },
  { path: 'ingredient/:action/:id', component: IngredientComponent },
  { path: 'tag/:action/:id', component: TagComponent },
  { path: 'feature/:action/:id', component: FeatureComponent },
  { path: 'country/:action/:id', component: CountryComponent },
  { path: 'measure/:action/:id', component: MeasureComponent },
];

export const routing = RouterModule.forRoot(routes);



