import { Injectable } from '@angular/core';
import { Http, Headers, URLSearchParams, Jsonp } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/toPromise';

import { Type } from './../enum';
import { SearchResult } from './../model';

@Injectable()
export class ApiService {
  title = 'Angular 2';
  language = null;
  shared: any = {
    tags: []
  };

  // will use this for TypeAhead and autocomplete feature
  constructor(private http: Http, private jsonp: Jsonp) {

  }

  /**
   * Searches for a model object.
   * @param term { string } - Search term
   * @param language { number } - Id of the language of the search term
   * @param type { string }(Optional) - Type of model object it should search
   * @return Observable<any[]> - List of observable model objects. Default (if term is empty) is empty observable list.
   */
  search(term: string, language: number, type: string): Observable<any[]> {
    if (term === '') {
      return Observable.of([]);
    }

    let params = new URLSearchParams();
    params.set('q', term);
    params.set('lang', language.toString());
    params.set('type', type);

    return this.http
      .get('/search', { search: params })
      .map(response => response.json());
  }
  
  /**
   * Retrieves all model objects.
   * @param language { number } - Id of the language - returned objects will be returned in this language.
   * @return Promise<any[]> - Promise of model objects
   */
  retrieveAll(language: number): Promise<any[]> {
    return this.http.get('/search?lang=' + language)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getLanguages(): Promise<any[]> {
    return this.http.get('language')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getProduct(id: Number): Promise<any> {
    return this.http.get('product/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getIngredients(): Promise<any[]> {
    return this.http.get('ingredient')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getIngredient(id: Number): Promise<any> {
    return this.http.get('ingredient/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getMeasures(): Promise<any[]> {
    return this.http.get('measure')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getMeasure(id: Number): Promise<any> {
    return this.http.get('measure/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getTags(): Promise<any[]> {
    return this.http.get('tag')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getTag(id: Number): Promise<any> {
    return this.http.get('tag/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getNutrients(): Promise<any[]> {
    return this.http.get('nutrient')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getNutrient(id: Number): Promise<any> {
    return this.http.get('nutrient/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getFeatures(): Promise<any[]> {
    return this.http.get('feature')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getFeature(id: Number): Promise<any> {
    return this.http.get('feature/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getCountries(): Promise<any[]> {
    return this.http.get('country')
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  getCountry(id: Number): Promise<any> {
    return this.http.get('country/' + id)
              .toPromise()
              .then(response => response.json())
              .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}



