import { Component, OnInit } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs/Rx'; 

import { Language, Translation, Tag, Feature, Measure, Nutrient, Country, Alert } from './shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './shared/service';

import '../style/app.scss';



@Component({
  selector: 'my-app', // <my-app></my-app>
  template: require('./app.component.html'),
  styles: [require('./app.component.scss')],
})
export class AppComponent {
  url = 'https://github.com/preboot/angular2-webpack';
  alerts: Alert[];
  

  constructor(private service: ApiService, private config: ConfigService, private shared: SharedService, private data: DataService) {
    // Do something with api
  }

  ngOnInit() {
    this.config.language = new Language(1, 'English', true);

    this.data.update();
    
    this.shared.alerts.subscribe(alerts => {
      this.alerts = alerts;
    });

  }

  public closeAlert(alert: Alert) {
    this.shared.deleteAlert(alert);
  };



  

}



