import { Component, Input, OnInit, Optional } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers, Response } from '@angular/http';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { Translation, Feature, Ingredient, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';


@Component({
  selector: 'my-ingredient',
  template: require('./ingredient.component.html'),
  styles: [require('./ingredient.component.scss')]
})
export class IngredientComponent implements OnInit {

  /**
   * router params
   * id - model id
   * action - "create" or "update"
   */


  ingredient: Ingredient = new Ingredient(0, '', [], [], false, []);

  features: any[];

  searching = false;
  searchFailed = false;
  searchTerm: any;

  @Input()
  public mdl: boolean = false;
  @Input()
  public final: boolean = false;

  public name: string = '';
  public action: string = 'new';
  public id: number = 0;

  constructor(private route: ActivatedRoute,
    private api: ApiService,
    private config: ConfigService,
    private shared: SharedService,
    private data: DataService,
    private http: Http,
    private modal: NgbModal,
    @Optional() public activeModal: NgbActiveModal) {
  }

  ngOnInit(): void {
    console.log("Ingredient component initialized!");

    this.features = this.data.features.map(f => {
      return {
        id: f.id,
        name: f.names.filter(tr => tr.language.id == this.config.language.id)[0].value
      }
    });

    if (!this.mdl) {
      this.route.params.forEach((params: Params) => {
        this.action = params['action'];
        this.id = params['id'];
      });
    }

    if (this.action == 'edit') {
      this.api.getIngredient(this.id).then(ingredient => {
        this.ingredient = new Ingredient(ingredient.id, ingredient.code, [], [], ingredient.parent, [])
        for (let i = 0; i < ingredient.info.length; i++) {
          let language = this.data.languages.filter(lang => lang.active && lang.id == ingredient.info[i].pivot.language_id)[0];
          if (language != undefined) {
            let translation = new Translation(language, ingredient.info[i].pivot.name); 
            this.ingredient.names.push(translation);
          }
        }

        for (let i = 0; i < ingredient.features.length; i++) {
          let feature = new Feature(
            <number>ingredient.features[i].pivot.feature_id,
            <number>ingredient.features[i].pivot.tag_id,
            [],
            <string>ingredient.features[i].pivot.rule);

          for (let j = 0; j < ingredient.features[i].info.length; j++) {
            let language = this.data.languages.filter(lang => lang.active && lang.id == ingredient.features[i].info[j].pivot.language_id)[0];
            if (language != undefined) {
              let translation = new Translation(language, ingredient.features[i].info[j].pivot.name);
              feature.names.push(translation);
            }
              
          }

          this.ingredient.features.push(feature);
        }

        for (let i = 0; i < ingredient.children.length; i++) {
          this.ingredient.children.push({
            id: ingredient.children[i].id,
            name: ingredient.children[i].code,
          })
        }
      });
    } else {
      for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
        if (this.mdl && this.data.languages[i].id == this.config.language.id)
          this.ingredient.names.push(new Translation(this.data.languages[i], this.name));
        else
          this.ingredient.names.push(new Translation(this.data.languages[i], ''));
      }
    }
  }

  save() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (this.action == 'edit')
      this.http.put('ingredient/' + this.ingredient.id, { data: this.ingredient }, { headers })
      .map((res: Response) => res.json())
      .subscribe((data) => {
        this.shared.addAlert(new Alert(0, "success", "Ingredient saved successfully!"));
        if (this.mdl)
            this.activeModal.close(data);
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Ingredient could not be saved. ERROR -> " + error._body));
      });
    else
      this.http.post('ingredient', { data: this.ingredient }, { headers })
        .map((res: Response) => res.json())
        .subscribe((data) => {
          console.log(data);
          this.shared.addAlert(new Alert(0, "success", "Ingredient saved successfully!"));
          if (this.mdl)
            this.activeModal.close(data);
        }, (error) => {
          this.shared.addAlert(new Alert(0, "danger", "Ingredient could not be saved. Error -> " + error._body));
        });
  }

  delete() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.put('ingredient/' + this.ingredient.id, {}, { headers })
      .subscribe(() => { }, () => {
        this.shared.addAlert(new Alert(0, "danger", "Ingredient could not be deleted due to foreign key constraints"));
      });
  }

  getFeatureName(id: number) {
    return this.ingredient.features.filter(f => f.id == id)[0].names.filter(tr => tr.language.id == this.config.language.id)[0].value;
  };

  changeFeatureValue(index: number, nfid: number) {
    this.ingredient.features[index] = this.data.features.filter(f => f.id == nfid)[0];
  }

  addFeature() {
    let feature = new Feature(0, 0, [], '');
    for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
      feature.names.push(new Translation(this.data.languages[i], "Choose one"));
    }
    this.ingredient.features.push(feature);
  }

  deleteFeature(index: number) {
    this.ingredient.features.splice(index, 1);
  }

  onSearchEnter($event, ingredient: any): void {
    $event.preventDefault();
    $event.stopPropagation();
    $event.cancelBubble = true;
    $event.stopImmediatePropagation();
    if (ingredient !== null && ingredient.id !== undefined)
      this.ingredient.children.push({ id: ingredient.id, name: ingredient.name });
    else
      this.openIngredientModal(ingredient, 0, 'new');
    
    this.searchTerm = "";
  }

  openIngredientModal(name: string, id: number, action: string): void {
    const modalRef = this.modal.open(IngredientComponent, { size: "sm" });
    modalRef.result.then((result) => {
      if (action == "new") {
        this.ingredient.children.push({ id: result.id, name: result.info.filter(l => l.pivot.language_id == this.config.language.id)[0].pivot.name });
      }
    })
    modalRef.componentInstance.mdl = true;
    modalRef.componentInstance.final = true;
    modalRef.componentInstance.name = name;
    modalRef.componentInstance.action = action;
    modalRef.componentInstance.id = id;
  }

  search = (text$: Observable<string>) => {
    return text$
      .debounceTime(300)
      .distinctUntilChanged()
      .do(() => this.searching = true)
      .switchMap(term => {
        return this.api.search(term, this.config.language.id, "ingredient")
          .do(() => {
            this.searchFailed = false
          })
          .catch(() => {
            this.searchFailed = true;
            return Observable.of([]);
          })
      })
      .do(() => this.searching = false);
  }

  formatter = (result: any) => result.name;

}



