import { Injectable } from '@angular/core';

import { Translation, Language, Tag, Measure, Feature, Nutrient, Country } from './../model';
import { SharedService, ApiService } from '.';

@Injectable()
export class DataService {
    languages: Language[] = [];
    tags: Tag[] = [];
    measures: Measure[] = [];
    features: Feature[] = [];
    nutrients: Nutrient[] = [];
    countries: Country[] = [];

    constructor(private api: ApiService, private shared: SharedService) {

    }

    update(): void {
        this.updateLanguages();
        this.updateNutrients();
        this.updateTags();
        this.updateFeatures();
        this.updateMeasures();
        this.updateCountries();
    }

    updateLanguages(): void {
        this.api.getLanguages().then(data => {
            this.languages = [];
            for (let i = 0; i < data.length; i++) {
                this.languages.push(new Language(<number>data[i].id, <string>data[i].name, <boolean>data[i].aes_active));
            }
        });
    }

    updateNutrients(): void {
        this.api.getNutrients().then(nutrients => {
            this.nutrients = [];
            for (let i = 0; i < nutrients.length; i++) {
                let nutrient = new Nutrient(nutrients[i].id, [], nutrients[i].dailyrec, nutrients[i].calories, nutrients[i].measure_id);
                for (let j = 0; j < nutrients[i].info.length; j++) {
                    let language = this.languages.filter(lang => lang.active && lang.id == nutrients[i].info[j].pivot.language_id)[0];
                    if (language != undefined) {
                        let translation = new Translation(language, nutrients[i].info[j].pivot.name);
                        nutrient.names.push(translation);
                    }
                }
                this.nutrients.push(nutrient);
            }
        });
    }

    updateTags(): void {
        this.api.getTags().then(tags => {
            this.tags = [];
            for (let i = 0; i < tags.length; i++) {
                let tag = new Tag(tags[i].id, tags[i].tag_id, []);
                for (let j = 0; j < tags[i].info.length; j++) {
                    let language = this.languages.filter(lang => lang.active && lang.id == tags[i].info[j].pivot.language_id)[0];
                    if (language != undefined) {
                        let translation = new Translation(language, tags[i].info[j].pivot.name);
                        tag.names.push(translation);
                    }
                }
                this.tags.push(tag);
            }
        });
    }

    updateFeatures(): void {
        this.api.getFeatures().then(features => {
            this.features = [];
            for (let i = 0; i < features.length; i++) {
                let feature = new Feature(features[i].id, features[i].tag_id, [], features[i].rule);
                for (let j = 0; j < features[i].info.length; j++) {
                    let language = this.languages.filter(lang => lang.active && lang.id == features[i].info[j].pivot.language_id)[0];
                    if (language != undefined) {
                        let translation = new Translation(language, features[i].info[j].pivot.name);
                        feature.names.push(translation);
                    }
                }
                this.features.push(feature);
            }
        });
    }

    updateMeasures(): void {
        this.api.getMeasures().then(measures => {
            this.measures = [];
            for (let i = 0; i < measures.length; i++) {
                let measure = new Measure(measures[i].id, measures[i].code, [], measures[i].short);
                for (let j = 0; j < measures[i].info.length; j++) {
                    let language = this.languages.filter(lang => lang.active && lang.id == measures[i].info[j].pivot.language_id)[0];
                    if (language != undefined) {
                        let translation = new Translation(language, measures[i].info[j].pivot.name);       
                        measure.names.push(translation);
                    }
                }
                this.measures.push(measure);
            }
        });
    }

    updateCountries(): void {
        this.api.getCountries().then(countries => {
            this.countries = [];
            for (let i = 0; i < countries.length; i++) {
                let country = new Country(countries[i].id, countries[i].code, []);
                for (let j = 0; j < countries[i].info.length; j++) {
                    let language = this.languages.filter(lang => lang.active && lang.id == countries[i].info[j].pivot.language_id)[0];
                    if (language != undefined) {
                        let translation = new Translation(language, countries[i].info[j].pivot.name);
                        country.names.push(translation);
                    }
                }
                this.countries.push(country);
            }
        });
    }
}


