import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers } from '@angular/http';

import { Translation, Tag, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';





@Component({
  selector: 'my-tag',
  template: require('./tag.component.html'),
  styles: [require('./tag.component.scss')]
})
export class TagComponent implements OnInit {

  /**
   * router params
   * id - model id
   * action - "create" or "update"
   */
  id: number;
  action: string;

  // tag model 
  tag: Tag = new Tag(0, 0, []);

  // parent dropdown tags
  tags: any[] = [];

  // parent tag
  parent: any = {
    id: 0,
    name: "Choose one...",
  };


  // constructor
  constructor(private route: ActivatedRoute,
    private http: Http,
    private api: ApiService,
    private data: DataService,
    private shared: SharedService,
    private config: ConfigService) {
  }

  // ngOnInit
  ngOnInit(): void {

    console.log("Initiated Tag Component");

    this.route.params.forEach((params: Params) => {
      this.action = params['action'];
      this.id = params['id'];
    });

    this.tags = this.data.tags.map(t => {
      return {
        id: t.id,
        name: t.names.filter(tr => {
          console.log(tr);
          return tr.language.id == this.config.language.id
        })[0].value
      }
    });

    if (this.action == 'edit') {
      this.api.getTag(this.id).then(tag => {
        this.tag = new Tag(tag.id, tag.tag_id, []);
        for (let i = 0; i < tag.info.length; i++) {
          let language = this.data.languages.filter(lang => lang.active && lang.id == tag.info[i].pivot.language_id)[0];
          if (language !== undefined) {
            let translation = new Translation(language, tag.info[i].pivot.name);
            this.tag.names.push(translation);
          }
        }

        if (tag.tag_id !== null && tag.tag_id !== undefined) {
          let parent = this.data.tags.filter(t => t.id == tag.tag_id)[0];
          this.parent.id = parent.id;
          this.parent.name = parent.names.filter(tr => tr.language.id == this.config.language.id)[0].value;
        }
      });
    } else {
      for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
        this.tag.names.push(new Translation(this.data.languages[i], ''));
      }
    }

  }

  // save 
  save() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (this.action == 'edit')
      this.http.put('tag/' + this.tag.id, {
        data: this.tag
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, "success", "Tag saved successfully!"));
        this.data.updateTags();
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Tag could not be saved. ERROR -> " + error._body));
      });
    else
      this.http.post('tag', {
        data: this.tag
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, "success", "Tag saved successfully!"));
        this.data.updateTags();
      }, (error) => {
        this.shared.addAlert(new Alert(0, "danger", "Tag could not be saved. ERROR -> " + error._body));
      });
  }

  // delete
  delete() {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.delete('tag/' + this.tag.id, { headers })
      .subscribe(() => { }, () => {
        this.data.updateTags();
        this.shared.addAlert(new Alert(0, "danger", "Tag could not be deleted due to foreign key constraints"));
      });
  }

  // update the parent of the tag
  changeParent(id: Number) {
    let parent = this.data.tags.filter(t => t.id == id)[0];
    this.parent.id = parent.id;
    this.tag.tag_id = this.parent.id;
    this.parent.name = parent.names.filter(tr => tr.language.id == this.config.language.id)[0].value;
  };

}



