import { NgModule, ApplicationRef } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule, JsonpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';

import {NgbModule, NgbTypeaheadConfig, NgbDropdownConfig} from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { SettingsComponent } from './settings/settings.component';
import { ProductComponent } from './product/product.component';
import { NutrientComponent } from './nutrient/nutrient.component';
import { IngredientComponent } from './ingredient/ingredient.component';
import { TagComponent } from './tag/tag.component';
import { FeatureComponent } from './feature/feature.component';
import { CountryComponent } from './country/country.component';
import { MeasureComponent } from './measure/measure.component';
import { ApiService, ConfigService, SharedService, DataService } from './shared/service';
import { routing } from './app.routing';

import { removeNgStyles, createNewHosts } from '@angularclass/hmr';

@NgModule({
  imports: [
    BrowserModule,
    HttpModule,
    JsonpModule,
    FormsModule,
    NgbModule.forRoot(),
    routing
  ],
  declarations: [
    AppComponent,
    HomeComponent,
    SettingsComponent,
    ProductComponent,
    NutrientComponent,
    IngredientComponent,
    TagComponent,
    FeatureComponent,
    MeasureComponent,
    CountryComponent,
  ],
  providers: [
    ApiService,
    DataService,
    ConfigService,
    SharedService,
    NgbTypeaheadConfig,
    NgbDropdownConfig,
  ],
  bootstrap: [AppComponent]
})
export class AppModule {

  constructor(public appRef: ApplicationRef) {}

  hmrOnInit(store) {
    console.log('HMR store', store);
  }

  hmrOnDestroy(store) {
    let cmpLocation = this.appRef.components.map(cmp => cmp.location.nativeElement);
    // recreate elements
    store.disposeOldHosts = createNewHosts(cmpLocation);
    // remove styles
    removeNgStyles();
  }

  hmrAfterDestroy(store) {
    // display new elements
    store.disposeOldHosts();
    delete store.disposeOldHosts;
  }
}




