import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';

import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';
import { Translation, Tag, SearchResult, Alert } from './../shared/model';
import { Type } from './../shared/enum';



@Component({
  selector: 'my-home',
  template: require('./home.component.html'),
  styles: [require('./home.component.scss')]
})
export class HomeComponent implements OnInit {

  result: SearchResult[];
  initialResult: SearchResult[];

  blank: Boolean = false;

  search_term: SearchResult;
  filter_term: string;
  searching = false;
  searchFailed = false;

  products: SearchResult[] = [];
  ingredients: SearchResult[] = [];
  nutrients: SearchResult[] = [];
  tags: SearchResult[] = [];
  features: SearchResult[] = [];
  countries: SearchResult[] = [];
  measures: SearchResult[] = [];
  

  constructor(private api: ApiService, private config: ConfigService, private shared: SharedService, private data: DataService, private router: Router) {

  }

  ngOnInit() {
    this.result = [];  

    this.api.retrieveAll(this.config.language.id).then(results => {
      for (let i = 0; i < results.length; i++) {
        this.result.push(new SearchResult(
          <Number>results[i].id, 
           <string>results[i].name,
           <Type>results[i].type
        ));
      }

      this.initialResult = this.result;

      this.categorize();

      if (this.data.measures.length == 0) {
        this.blank = true;
        this.shared.addAlert(new Alert(0, 'success', 'You need measures before creating anything. Click "Create new..." and choose "Measures".'));
      }
  });
  }

  inputFormatter = (v: SearchResult) => v.name;

  resultFormatter = (v: SearchResult) => v.name + " (" + Type[v.type] + ")";

  onSearchEnter(searchResult: SearchResult) {
    if (this.blank && Type[searchResult.type].toLowerCase() != 'measure') {
      this.shared.addAlert(new Alert(0, 'success', 'You need to add some measures first'));
      return;
    }
    this.router.navigate(['/' + Type[searchResult.type].toLowerCase() + '/edit', searchResult.id]);
  }

  createNew (option: string) {
    if (this.blank && option != 'measure') {
      this.shared.addAlert(new Alert(0, 'success', 'You need to add some measures first'));
      return;
    }
    this.router.navigate(['/' + option + '/new/0']);
  }

  navigateTo(result: SearchResult) {
    if (this.blank && Type[result.type].toLowerCase() != 'measure') {
      this.shared.addAlert(new Alert(0, 'success', 'You need to add some measures first'));
      return;
    }

    this.router.navigate(['/' + Type[result.type].toLowerCase() + '/edit/' + result.id]);
  }

  search = (text$: Observable<string>) => {
    return text$
      .debounceTime(300)
      .distinctUntilChanged()
      .do(() => this.searching = true)
      .switchMap(term => {
        return this.api.search(term, this.config.language.id, "all")
            .do(() => {
              this.searchFailed = false
            })
            .catch(() => {
              this.searchFailed = true;
              return Observable.of([]);
            })})
      .do(() => this.searching = false);
  }
  
  filter = (filter_term) => {
    if (filter_term == "") {
      this.result = this.initialResult;
      this.categorize();
    } else {
    this.api.search(filter_term, this.config.language.id, "all")
        .do(() => {
          this.searchFailed = false;
        })
        .catch(() => {
          this.searchFailed = true;
          return Observable.of([]);
        }).subscribe(
          data => {
            this.result = [];
            
            for (let i = 0; i < data.length; i++) {
              this.result.push(new SearchResult(
                <Number>data[i].id, 
                <string>data[i].name,
                <Type>data[i].type
              ));
            }

            this.categorize();
          }
        )};
  }

  categorize = () => {
    this.products = this.result.filter(result => result.type == Type.Product);
    this.ingredients = this.result.filter(result => result.type == Type.Ingredient);
    this.nutrients = this.result.filter(result => result.type == Type.Nutrient);
    this.tags = this.result.filter(result => result.type == Type.Tag);
    this.features = this.result.filter(result => result.type == Type.Feature);
    this.countries = this.result.filter(result => result.type == Type.Country);
    this.measures = this.result.filter(result => result.type == Type.Measure);
  }


}



