export class Translation {
    language: Language;
    value: string;

    constructor(language: Language, value: string) {
        this.language = language;
        this.value = value;
    }
}

export class Language {
    id: number;
    name: string;
    active: boolean

    constructor(id: number, name: string, active: boolean) {
        this.id = id;
        this.name = name;
        this.active = active;
    }
}

export class Product {
    id: number;
    manufacturer: string;
    weight: number;
    content: number;
    measure_id: number;
    country_id: number;
    content_netto: number;
    measure_netto_id: number;
    nutrient_netto: number;
    nutrient_netto_id: number;
    nutrients: Nutrient[];
    ingredients: Ingredient[];
    tags: Tag[];
    names: Translation[];
    texts: Translation[];
    posttexts: Translation[];

    constructor(id: number,
        manufacturer: string,
        weight: number,
        content: number,
        measure_id: number,
        country_id: number,
        content_netto: number,
        measure_netto_id: number,
        nutrient_netto: number,
        nutrient_netto_id: number,
        nutrients: Nutrient[],
        ingredients: Ingredient[],
        tags: Tag[],
        names: Translation[],
        texts: Translation[],
        posttexts: Translation[]) {
        this.id = id;
        this.manufacturer = manufacturer;
        this.weight = weight;
        this.content = content;
        this.measure_id = measure_id;
        this.country_id = country_id;
        this.content_netto = content_netto;
        this.measure_netto_id = measure_netto_id;
        this.nutrient_netto = nutrient_netto;
        this.nutrient_netto_id = nutrient_netto_id;
        this.nutrients = nutrients;
        this.ingredients = ingredients;
        this.tags = tags;
        this.names = names;
        this.texts = texts;
        this.posttexts = posttexts;
    }
}
export class Ingredient {
    id: number;
    code: string;
    names: Translation[];
    features: Feature[];
    parent: boolean;
    children: any[];

    constructor(id: number, code: string, names: Translation[], features: Feature[], parent: boolean, children: any[]) {
        this.id = id;
        this.code = code;
        this.names = names;
        this.features = features;
        this.parent = parent;
        this.children = children;
    }
}
export class Nutrient {
    id: number;
    names: Translation[];
    dailyrec: number;
    calories: number;
    measure_id: number;

    constructor(id: number, names: Translation[], dailyrec: number, calories: number, measure_id: number) {
        this.id = id;
        this.names = names;
        this.dailyrec = dailyrec;
        this.calories = calories;
        this.measure_id = measure_id;
    }
}
export class Tag {
    id: number;
    tag_id: number;
    names: Translation[];

    constructor(id: number, tag_id: number, names: Translation[]) {
        this.id = id;
        this.tag_id = tag_id;
        this.names = names;
    }
}
export class Feature {
    id: number;
    tag_id: number;
    names: Translation[];
    rule: string;

    constructor(id: number, tag_id: number, names: Translation[], rule: string) {
        this.id = id;
        this.tag_id = tag_id;
        this.names = names;
        this.rule = rule;
    }
}
export class Country {
    id: number;
    code: string;
    names: Translation[];

    constructor(id: number, code: string, names: Translation[]) {
        this.id = id;
        this.code = code;
        this.names = names;
    }
}
export class Measure {
    id: number;
    code: string;
    names: Translation[];
    short: string;

    constructor(id: number, code: string, names: Translation[], short: string) {
        this.id = id;
        this.code = code;
        this.names = names;
        this.short = short;
    }
}

export class Alert {
    id: number;
    type: string;
    message: string;

    constructor(id: number, type: string, message: string) {
        this.id = id;
        this.type = type;
        this.message = message;
    }
}




