import { Component, OnInit } from '@angular/core';
import { Http, Headers } from '@angular/http';

import { Alert, Translation, Language } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';



@Component({
  selector: 'my-settings',
  template: require('./settings.component.html'),
  styles: [require('./settings.component.scss')]
})
export class SettingsComponent implements OnInit {

  languages: Language[];
  activeLanguages: Language[];
  selected: Language;

  constructor(
    private http: Http,
    private service: ApiService, 
    private config: ConfigService, 
    private shared: SharedService, 
    private data: DataService) {
    
  }

  ngOnInit() {
    this.selected = this.config.language;
    this.languages = this.data.languages;
    this.activeLanguages = this.data.languages.filter(l => l.active);
  }

  save(language) {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.put('language/' + language.id, {
      data: {
        language: language
      }
    }, { headers }).subscribe(() => {
      this.shared.addAlert(new Alert(0, "success", "Language saved successfully!"));
      this.data.update();
    }, (error) => {
      this.shared.addAlert(new Alert(0, "danger", "Language could not be saved. ERROR -> " + error._body));
    });
  }

  switch(language) {
    this.selected = language;
    this.config.language = language;
  }

  toggleLanguage(language) {
    this.save(language);
    this.activeLanguages = this.data.languages.filter(l => l.active);
    // update selected
  }

}



